
.title{
	width: 160px;
	position: relative;
	margin-bottom: 70px;
}
.title:before,.title:after{
	content: '';
	height: 1px;
	background-color: #333;
	width: 184px;
	position: absolute;
	top: 66px;
}
.title:before{
	left: -184px;
}
.title:after{
	right: -184px;
}
h2{
	margin: 6px 0 0 0;
	font-weight: normal;
}



