

















































































/*默认宽度大于800*/
.navigator[data-v-18b78165] {
    width: 100%;
    height: 86px;
    background-color: #000;
    z-index: 10;
	position: fixed;
	top: 0;
	left: 0
}
ul[data-v-18b78165]{
    margin: 0;
    padding: 0;
    width: 100%;
	list-style: none;
	box-sizing: border-box;
	font-size: 16px;
}
.nav[data-v-18b78165] {
	box-sizing: border-box;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	padding-right: 45px;
	background: url(../assets/logotop.png) no-repeat 24px center;
    background-size: 150px;
}
.nav li[data-v-18b78165] {
	margin-left: 18px;
	font-weight: bold;
	cursor: pointer;
}
a[data-v-18b78165]{
	text-decoration: none;
	color: #fff;
}
#demo[data-v-18b78165] {
	border: solid 2px #fff;
	padding: 8px 15px;
	border-radius: 50px;
}
.switch[data-v-18b78165] {
	display: none;
	cursor: pointer;
}
/*汉堡动画*/
.firstActive[data-v-18b78165] {
	transition: all 0.5s;
	-webkit-transform: translate(0px,10px) rotate(45deg);
	        transform: translate(0px,10px) rotate(45deg);
	background-color: #888!important;
}
.secondActive[data-v-18b78165] {
	transition: all 0.5s;
	opacity: 0;
	background-color: #888!important;
}
.thirdActive[data-v-18b78165] {
	transition: all 0.5s;
	-webkit-transform: translate(0px,-10px) rotate(-45deg);
	        transform: translate(0px,-10px) rotate(-45deg);
	background-color: #888!important;
}

/*屏幕小于800时*/
@media screen and (max-width: 800px) {
.navigator[data-v-18b78165] {
		height: 48px;
		background: url(../assets/logotop.png) no-repeat 98% 5px #000;
		background-size: 100px;
		position: absolute;
}
.nav[data-v-18b78165] {
		display: block;
		transition: height 0.2s, padding 0.3s;
		position: fixed;
		padding: 0 24px 0 24px;
		background: #fff;
		height: 0px;
		overflow: hidden;
		left: 0;
		top: 0;
}
.nav a[data-v-18b78165]{
		color: #888;
		display: block;
		width: 100%;
		height: 100%;
}
.nav li[data-v-18b78165] {
		height: 46px;
		line-height: 46px;
		margin: 0;
		padding: 0 0 0 30px;
		border-top: solid 1px #eee;
		text-align: left;
}
#demo[data-v-18b78165] {
		height: 86px;
		padding: 0;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		    -ms-flex-align: center;
		        align-items: center;
		-webkit-box-pack: center;
		    -ms-flex-pack: center;
		        justify-content: center;
		border: none;
		border-top: 1px solid #eee;
		border-radius: 0;
}
#demo a[data-v-18b78165] {
		height: 46px;
		background-color: #2eb6dc;
		width: 100%;
		border-radius: 40px;
		text-align: center;
		color: #fff;
}
.show[data-v-18b78165] {
		height: 396px;
		padding: 74px 24px 0 24px;
}
.switch[data-v-18b78165] {
		display: block;
		position: fixed;
		width: 40px;
		height: 40px;
		background-color: #888888;
		left: 10px;
		top: 10px;
		border-radius: 5px;
}
.switch span[data-v-18b78165] {
		display: block;
		height: 2px;
		margin: 8px 6px;
		background-color: #ccc;
		transition: all 0.5s;
}
.showBackground[data-v-18b78165]{
		background-color: #444!important
}
}
/*屏幕宽度大于1366时,定宽为1366居中*/
@media screen and (min-width: 1366px) {
.nav[data-v-18b78165]{
		width: 1366px;
		margin: 0 auto;
}
}



